/* hero-section css start */
.hero {
  position: relative;
  &__content {
    position: relative;
    z-index: 9;
    p {
      color: #ffffff;
      margin-top: 25px;
      font-size: 18px;
      @media (max-width: 767px) {
        font-size: 16px; } } }
  &__title {
    font-size: 72px;
    font-weight: 700;
    color: #ffffff;
    @media (max-width: 1199px) {
      font-size: 62px; }
    @media (max-width: 767px) {
      font-size: 56px; }
    @media (max-width: 575px) {
      font-size: 48px; }
    @media (max-width: 480px) {
      font-size: 42px; }
    @media (max-width: 320px) {
      font-size: 36px; } }
  &__slider {
    .slick-arrow {
      position: absolute;
      top: 50%;
      z-index: 99;
      width: 80px;
      height: 40px;
      color: #ffffff;
      font-size: 42px;
      margin-top: -20px;
      @include d-flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
      &:hover {
        &::before {
          width: 55px;
          height: 55px; } }
      &::before {
        position: absolute;
        content: '';
        top: 50%;
        left: 50%;
        width: 40px;
        height: 40px;
        background-color: $base-color;
        opacity: 0.25;
        @include border-radius(50%);
        z-index: -1;
        @include transform(translate(-50%, -50%));
        @include transition(all 0.3s); }
      &.prev {
        left: 50px;
        &::before {
          left: 35%; } }
      &.next {
        right: 50px;
        &::before {
          left: 65%; } } }
    .slick-dots {
      position: absolute;
      bottom: 45px;
      width: 100%;
      justify-content: center;
      li {
        button {
          height: 4px;
          width: 50px;
          @include border-radius(0);
          background-color: rgba(#ffffff, 0.35); } } } }
  .single__slide {
    padding-top: 335px;
    padding-bottom: 250px;
    position: relative;
    z-index: 1;
    @media (max-width: 1199px) {
      padding-top: 275px;
      padding-bottom: 195px; }
    @media (max-width: 767px) {
      padding-top: 245px;
      padding-bottom: 145px; }
    @media (max-width: 575px) {
      padding-bottom: 130px; }
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #000000;
      z-index: -1;
      opacity: 0.65; } } }
/* hero-section css edn */

/* inner-page-hero css start */
.inner-page-hero {
  padding-top: 265px;
  padding-bottom: 200px;
  position: relative;
  z-index: 1;
  background-position-y: center;
  @media (max-width: 1199px) {
    padding-top: 215px;
    padding-bottom: 130px; }
  @media (max-width: 575px) {
    padding-bottom: 100px; }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-one;
    opacity: 0.85;
    z-index: -1; }
  .page-title {
    font-weight: 600;
    color: #ffffff; }
  .page-list {
    @include d-flex;
    li {
      padding: 0 15px;
      position: relative;
      color: rgba(#ffffff, 0.7);
      &:first-child {
        padding-left: 0;
        &::before {
          display: none; } }
      &::before {
        position: absolute;
        content: '/';
        color: #ffffff;
        left: 0px; }
      a {
        color: #ffffff; } } } }
/* inner-page-hero css end */
