/* header start */
.header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 9999999;
  @include transition(all 0.3s);
  &.active {
    background-color: #070B28; }
  &.menu-fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 9999;
    .header__top {
      display: none; }
    .header__bottom {
      box-shadow: 0 2px 5px 0 rgba(0,0,0,0.1);
      background-color: #070B28;
      padding: 5px 0; } }
  &__top {
    padding: 15px 0;
    border-bottom: 1px solid rgba(#ffffff, 0.19);
    @media (max-width: 1199px) {
      padding: 8px 0; }
    .left {
      @media (max-width: 575px) {
        justify-content: center; }
      a {
        color: #ffffff;
        @media (max-width: 1199px) {
          font-size: 14px; } }
      .language {
        margin-left: 30px;
        @include d-flex;
        align-items: center;
        i {
          color: #ffffff;
          font-size: 18px;
          margin-right: 5px;
          @media (max-width: 1199px) {
            font-size: 15px; } }
        .nice-select {
          width: auto;
          background-color: transparent;
          padding-left: 0;
          padding-right: 10px;
          min-height: 40px;
          line-height: 47px;
          border: none;
          &::after {
            right: 0;
            margin-top: -2px; }
          .list {
            background-color: $bg-one;
            box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.35);
            width: 75px; }
          .option {
            background-color: transparent;
            padding-left: 10px;
            padding-right: 10px;
            color: #ffffff; }
          .current {
            color: #ffffff; } } } }
    .right {
      a {
        color: #ffffff;
        margin: 5px 15px;
        &:last-child {
          margin-right: 0; } } } }
  &__bottom {
    @include transition(all 0.3s);
    @media (max-width: 1199px) {
      padding: 10px 0; } }
  .site-logo {
    img {
      @media (max-width: 1199px) {
        max-width: 150px; } } }
  .main-menu {
    @media (max-width: 1199px) {
      padding: 15px 0; }
    li {
      position: relative;
      &.menu_has_children {
        position: relative;
        &.open {
          .sub-menu {
            display: block; } }
        >a {
          @media (max-width: 1199px) {
            display: block; }
          &::before {
            position: absolute;
            content: "\f107";
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            top: 0;
            right: 0;
            color: #ffffff;
            @include transition(all 0.3s);
            display: none;
            @media (max-width: 1199px) {
              display: block;
              top: 9px; } } }
        &:hover {
          >a::before {
            @include transform(rotate(180deg));
            color: $base-color; } } }
      a {
        padding: 34px 15px 34px 0;
        text-transform: uppercase;
        font-size: 16px;
        color: #e7e7f4;
        position: relative;
        @media (max-width: 1199px) {
          padding: 8px 0;
          display: block;
          border-bottom: 1px solid rgba(#ffffff, 0.1); }
        &:hover,&:focus {
          color: $base-color; } }
      .sub-menu {
        position: absolute;
        width: 220px;
        top: 105%;
        left: -20px;
        z-index: 9999;
        background-color: darken($base-color, 5%);
        padding: 10px 0;
        border-top: 2px solid $base-color;
        -webkit-box-shadow: 0px 18px 54px -8px rgba(0,0,0,0.15);
        box-shadow: 0px 18px 54px -8px rgba(0,0,0,0.15);
        @include border-radius(5px);
        @include transition(all 0.3s);
        opacity: 0;
        visibility: hidden;
        border-top: 2px solid #ffffff;
        @media (max-width: 1199px) {
          opacity: 1;
          visibility: visible;
          display: none;
          position: static;
          @include transition(none);
          width: 100%; }

        li {
          a {
            padding: 8px 20px;
            display: block;
            color: #ffffff;
            font-size: 14px;
            &:hover {
              background-color: rgba(#ffffff, 0.05);
              color: #ffffff;
              text-decoration: underline; } } }
        li+li {
          margin-left: 0; } }
      &:hover .sub-menu {
        top: 100%;
        opacity: 1;
        visibility: visible; } }
    li+li {
      margin-left: 20px;
      @media (max-width: 1199px) {
        margin-left: 0; } } }
  .nav-right {
    margin-left: 30px;
    @media (max-width: 1199px) {
      margin-left: 0; }
    .cmn-btn {
      @include d-flex;
      align-items: center;
      @media (max-width: 1199px) {
        display: inline-block;
        margin-bottom: 15px; }
      i {
        margin-right: 6px;
        font-size: 22px;
        color: #ffb200; } } } }
.navbar-collapse {
  @media (max-width: 767px) {
    max-height: 320px;
    overflow: auto; } }
.navbar-toggler {
  padding: 0;
  border: none;
  &:focus {
    outline: none; } }
.menu-toggle {
  margin: 10px 0;
  position: relative;
  display: block;
  width: 35px;
  height: 20px;
  cursor: pointer;
  background: transparent;
  border-top: 2px solid;
  border-bottom: 2px solid;
  color: #000000;
  font-size: 0;
  @include transition(all 0.25s ease-in-out);
  cursor: pointer;
  @media (max-width: 1199px) {
    color: #ffffff; } }
.menu-toggle:before, .menu-toggle:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  position: absolute;
  top: 50%;
  left: 50%;
  background: currentColor;
  @include transform(translate(-50%, -50%));
  transition: -webkit-transform 0.25s ease-in-out;
  -webkit-transition: -webkit-transform 0.25s ease-in-out;
  -o-transition: -webkit-transform 0.25s ease-in-out;
  transition: transform 0.25s ease-in-out;
  -moz-transition: -webkit-transform 0.25s ease-in-out;
  -ms-transition: -webkit-transform 0.25s ease-in-out;
  @media (max-width: 1199px) {
    background-color: #ffffff; } }
span.is-active {
  border-color: transparent; }
span.is-active:before {
  @include transform(translate(-50%, -50%) rotate(45deg)); }
span.is-active:after {
  @include transform(translate(-50%, -50%) rotate(-45deg)); }
span.menu-toggle:hover {
  color: #000000;
  @media (max-width: 1199px) {
    color: #ffffff; } }
span.is-active {
  border-color: transparent; }
span.is-active:before {
  @include transform(translate(-50%, -50%) rotate(45deg)); }
span.is-active:after {
  @include transform(translate(-50%, -50%) rotate(-45deg)); }
/* header end */
