@import "./_reset";
/* global css strat */
// margin global css
.mt-15 {
  margin-top: 15px; }
.mt-20 {
  margin-top: 20px; }
.mt-30 {
  margin-top: 30px; }
.mt-40 {
  margin-top: 40px; }
.mt-50 {
  margin-top: 50px; }
.mt-80 {
  margin-top: 80px; }

.mb-15 {
  margin-bottom: 15px; }
.mb-20 {
  margin-bottom: 20px; }
.mb-30 {
  margin-bottom: 30px; }
.mb-50 {
  margin-bottom: 50px; }
.mb-none-30 {
  margin-bottom: -30px; }
.mb-none-50 {
  margin-bottom: -50px; }
.mt-minus-100 {
  margin-top: -100px; }

// padding global css
.pt-90 {
  padding-top: 90px; }
.pb-90 {
  padding-bottom: 90px; }
.pt-120 {
  padding-top: 120px;
  @media (max-width: 991px) {
    padding-top: 90px; }
  @media (max-width: 575px) {
    padding-top: 80px; } }
.pb-120 {
  padding-bottom: 120px;
  @media (max-width: 991px) {
    padding-bottom: 90px; }
  @media (max-width: 575px) {
    padding-bottom: 80px; } }
.pt-150 {
  padding-top: 150px;
  @media (max-width: 1199px) {
    padding-top: 120px; }
  @media (max-width: 991px) {
    padding-top: 90px; }
  @media (max-width: 575px) {
    padding-top: 80px; } }
.pb-150 {
  padding-bottom: 150px;
  @media (max-width: 1199px) {
    padding-bottom: 120px; }
  @media (max-width: 991px) {
    padding-bottom: 90px; }
  @media (max-width: 575px) {
    padding-bottom: 80px; } }
.mt-100 {
  margin-top: 100px; }
.container {
  @media (min-width: 1360px) {
    max-width: 1240px; } }
.bg_img {
  background-size: cover;
  background-position: center; }
.bg--one {
  background-color: $bg-one!important; }
.bg--two {
  background-color: $bg-two!important; }
.z-1 {
  z-index: 1; }
.color--1 {
  color: $base-color !important; }
.color--2 {
  color: #f32424 !important; }
.color--3 {
  color: #b013c3 !important; }
.color--4 {
  color: #1178d0 !important; }
.has-link {
  position: relative;
  .item-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999; } }
.top-shape {
  position: absolute;
  top: -2px;
  left: 0;
  img {
    width: 100%; } }
.bottom-shape {
  position: absolute;
  bottom: -3px;
  left: 0;
  img {
    width: 100%; } }
.base--bg {
  background-color: $base-color !important; }
.section-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.35;
  img {
    width: 100%;
    height: 100%;
    @include object-fit; } }
.overlay-one {
  position: relative;
  z-index: 9;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-two;
    opacity: 0.65;
    z-index: -1; } }
.overlay--one,
.overlay--two,
.overlay--three, {
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.65; } }
.overlay--one {
  &::before {
    background-color: $base-color; } }
.overlay--two {
  &::before {
    background-color: #ffab02; } }
.overlay--three {
  &::before {
    background-color: #ab1642; } }
.slice-shape {
  clip-path: polygon(0 0, 100% 8%, 100% 100%, 0% 100%);
  @media (max-width: 1199px) {
    clip-path: polygon(0 0, 100% 5%, 100% 100%, 0% 100%); }
  @media (max-width: 991px) {
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0% 100%); } }
select {
  option {
    background-color: $bg-two; } }
.nice-select {
  width: 100%;
  min-height: 50px;
  line-height: 50px;
  @include border-radius(3px);
  .list {
    width: 100%;
    box-shadow: 0 2px 15px 0 rgba(0,0,0,0.15); } }
.para-white {
  color: #e6e6e6!important; }
.heading--color {
  color: #363636!important; }
.section--bg {
  background-color: #f7f7f7; }
.section--bg-two {
  background-color: #fafafa; }
.bg--base {
  background-color: $base-color; }
.has--img {
  position: relative;
  .section--img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.03; } }
.section-header {
  margin-bottom: 70px;
  margin-top: -8px;
  @media (max-width: 991px) {
    margin-bottom: 50px; }
  p {
    font-size: 18px;
    @media (max-width: 991px) {
      font-size: 16px; } } }
.section-sub-title {
  font-size: 20px;
  font-family: $para-font;
  color: $base-color;
  @media (max-width: 767px) {
    font-size: 20px; } }
.section-title {
  font-size: 40px;
  font-weight: 700;
  text-transform: capitalize;
  @media (max-width: 991px) {
    font-size: 36px; }
  @media (max-width: 575px) {
    font-size: 32px; } }
.section-title + p {
  margin-top: 15px; }
.border-radius-100 {
  @include border-radius(50%!important); }
.hover--effect-1 {
  @include transition(all 0.3s);
  &:hover {
    @include transform(translateY(-5px) scale(1.05)); } }
.video-button {
  position: absolute;
  width: 105px;
  height: 105px;
  color: #ffffff;
  @include border-radius(50%);
  background-color: $base-color;
  @include d-flex;
  justify-content: center;
  align-items: center;
  @include animation(pulse 2000ms linear infinite);
  &:hover {
    color: #ffffff; }
  &::before, &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    border-radius: 74px;
    background-color: $base-color;
    opacity: 0.15;
    z-index: -10; }
  &::before {
    z-index: -10;
    @include animation(inner-ripple 2000ms linear infinite); }
  &::after {
    z-index: -10;
    @include animation(outer-ripple 2000ms linear infinite); }
  i {
    font-size: 52px; } }
@include keyframes(outer-ripple) {
  0% {
    @include transform(scale(1));
    opacity: 0.5; }
  80% {
    @include transform(scale(1.5));
    opacity: 0; }
  100% {
    @include transform(scale(2.5));
    opacity: 0; } }

@include keyframes(inner-ripple) {
  0% {
    @include transform(scale(1));
    opacity: 0.5; }
  30% {
    @include transform(scale(1));
    opacity: 0.5; }
  100% {
    @include transform(scale(1.5));
    opacity: 0; } }

.d-pagination {
  margin-top: 50px;
  .pagination {
    margin: -5px;
    li {
      margin: 5px 5px;
      &.active {
        a {
          background-color: $base-color;
          color: #ffffff;
          &:hover {
            background-color: $base-color;
            color: #ffffff; } } }
      a {
        @include border-radius(50%!important);
        color: $para-color;
        font-weight: 500;
        width: 45px;
        height: 45px;
        border: 1px solid #e5e5e5;
        text-align: center;
        line-height: 28px;
        &:hover {
          color: $base-color;
          background-color: transparent;
          border-color: $base-color; } } } } }
.nav-tabs--style {
  border: none;
  margin: -3px -10px;
  .nav-item {
    margin: 3px 10px;
    .nav-link {
      padding: 14px 30px;
      border: none;
      color: $para-color;
      @include border-radius(0);
      position: relative;
      background-color: #f1f1f1;
      &.active {
        background-color: $base-color;
        color: #ffffff; } } } }
.cmn-list {
  margin-top: 20px;
  li+li {
    margin-top: 15px; }
  li {
    position: relative;
    padding-left: 40px;
    &::before {
      position: absolute;
      content: '';
      top: 5px;
      left: 0;
      width: 20px;
      height: 10px;
      border-left: 1px solid $base-color;
      border-bottom: 1px solid $base-color;
      @include transform(rotate(-45deg)); } } }
.cmn-list-2 {
  margin-top: 20px;
  li+li {
    margin-top: 10px; }
  li {
    position: relative;
    padding-left: 45px;
    &:nth-last-of-type(3n+3) {
      &::before {
        border-color: #a22546; } }
    &:nth-last-of-type(3n+2) {
      &::before {
        border-color: #f7a139; } }
    &:nth-last-of-type(3n+1) {
      &::before {
        border-color: #3097d1; } }
    &::before {
      position: absolute;
      content: '';
      top: 5px;
      left: 0;
      width: 20px;
      height: 10px;
      border-left: 1px solid red;
      border-bottom: 1px solid red;
      @include transform(rotate(-45deg)); } } }
.number-list--style {
  list-style-type: decimal;
  padding-left: 15px;
  li {
    span {
      font-weight: 500;
      color: $base-color; } } }
.pagination {
  margin-top: 50px;
  li+li {
    margin-left: 10px; }
  li {
    &.active {
      a {
        background-color: $base-color;
        border-color: $base-color;
        color: #ffffff; } }
    a {
      border: 1px solid $border-color;
      @include border-radius(3px!important);
      padding: 12px 25px;
      background-color: transparent;
      color: $para-color;
      font-weight: 700;
      &:hover {
        background-color: $base-color;
        border-color: $base-color;
        color: #ffffff; } } } }
.cmn-accordion {
  .card+.card {
    margin-top: 15px; }
  .card {
    border: none;
    background-color: #ffffff;
    @include border-radius(5px !important);
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.1); }
  .card-header {
    background-color: transparent;
    padding: 0;
    margin-bottom: 0!important;
    border: 1px solid rgba(#ffffff, 0.1);
    @include border-radius(5px!important);
    .btn {
      background-color: #ffffff;
      padding: 10px 20px;
      display: inline-flex;
      display: -ms-inline-flexbox;
      display: -webkit-inline-flex;
      align-items: center;
      &:hover, &:focus {
        text-decoration: none;
        box-shadow: none; }
      i {
        color: $heading-color;
        font-size: 32px; }
      span {
        color: $heading-color;
        font-size: 20px;
        padding-left: 15px;
        @media (max-width: 767px) {
          font-size: 18px; } } } }
  .card-body {
    border-top: 1px solid #e5e5e5;
    padding: 20px 30px; } }

.cmn-accordion2 {
  .card+.card {
    margin-top: 30px; }
  .card {
    border: none;
    background-color: transparent;
    background-color: #ffffff;
    @include border-radius(0); }
  .card-header {
    background-color: #ffffff;
    margin-bottom: 0!important;
    border: none;
    padding: 0;
    @include border-radius(5px!important);
    .acc-btn {
      display: block;
      width: 100%;
      padding: 15px 30px;
      cursor: pointer;
      position: relative;
      text-align: left;
      border: 1px solid $border-color;
      background-color: #ffffff;
      &:focus {
        outline: none; }
      .text {
        font-weight: 700;
        color: $heading-color;
        font-size: 18px;
        @media (max-width: 575px) {
          font-size: 15px; } } } }
  .card-body {
    padding: 25px 0 0 0; } }
blockquote {
  p {
    font-weight: 700;
    font-size: 24px;
    color: #ffffff; } }
input:focus,
textarea:focus,
.nice-select.open {
  border-color: $base-color; }

.page-breadcrumb {
  @include d-flex;
  margin-top: 15px;
  li {
    color: #ffffff;
    text-transform: capitalize;
    &::after {
      content: '-';
      color: #ffffff;
      margin: 0 5px; }
    &:first-child {
      &::before {
        content: "\f102";
        font-family: 'Flaticon';
        color: #ef428c;
        margin-right: 6px; } }
    &:last-child {
      &::after {
        display: none; } }
    a {
      color: #ffffff;
      text-transform: capitalize; } } }
// custom animation
@include keyframes(customBounce) {
  0% {
    @include transform(translateY(0)); }
  25% {
    @include transform(translateY(-10px)); }
  50% {
    @include transform(translateY(-20px)); }
  75% {
    @include transform(translateY(-10px)); }
  100% {
    @include transform(translateY(0)); } }


#lightcase-overlay {
  z-index: 9999; }
#lightcase-case {
  z-index: 99999; }

.scroll-to-top {
  height: 60px;
  width: 60px;
  position: fixed;
  bottom: 5%;
  right: 5%;
  display: none;
  z-index: 99999;
  cursor: pointer;
  text-align: center;
  border-radius: 50%;
  background-color: $base-color;
  line-height: 77px;
  box-shadow: 0 5px 15px 0 rgba(#000000, 0.25);
  .scroll-icon {
    font-size: 31px;
    color: #ffffff;
    display: inline-block;
    i {
      @include transform(rotate(-45deg)); } } }

a[class*='lightcase-icon-'].lightcase-icon-close {
  top: 100px;
  right: 50px; }

// preloader css
$scale: 3;
$squareSize: 50px;
$cornerSize: ceil($squareSize / $scale);
$cornerPosition: ceil($cornerSize / $scale);
@keyframes dyinglight {
  15% {
    transform: scale(1.6); }
  50% {
    transform: rotate(-89deg); }
  100% {
    transform: rotate(-90deg); } }
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $base-color;
  z-index: 99999999; }
.dl {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: inline-block; }

.dl__square {
  display: block;
  width: $squareSize;
  height: $squareSize;
  background: #ffffff; }

.dl__container {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  transform-origin: 50% 50% 0;
  animation: dyinglight 1s ease infinite;
  backface-visibility: hidden; }

.dl__corner--top:before,
.dl__corner--top:after,
.dl__corner--bottom:before,
.dl__corner--bottom:after {
  position: absolute;
  width: $cornerSize;
  height: $cornerSize;
  color: #ffffff;
  content: ''; }

.dl__corner--top {
  &:before {
    border-left: 1px solid;
    border-top: 1px solid;
    top: -$cornerPosition;
    left: -$cornerPosition; }
  &:after {
    border-right: 1px solid;
    border-top: 1px solid;
    top: -$cornerPosition;
    right: -$cornerPosition; } }

.dl__corner--bottom {
  &:before {
    border-left: 1px solid;
    border-bottom: 1px solid;
    bottom: -$cornerPosition;
    left: -$cornerPosition; }
  &:after {
    border-right: 1px solid;
    border-bottom: 1px solid;
    bottom: -$cornerPosition;
    right: -$cornerPosition; } }
/* global css end */
