/* form css start */
.form-control {
  padding: 10px 20px;
  border: 1px solid $border-color;
  width: 100%;
  background-color: #f9f9f9;
  @include placeholder($para-color);
  @include border-radius(3px);
  color: $para-color;
  min-height: 50px;
  &:focus {
    background-color: transparent;
    box-shadow: none;
    border-color: $base-color; } }
select {
  padding: 10px 20px;
  width: 100%;
  border: 1px solid $border-color;
  cursor: pointer;
  color: $para-color;
  background-color: transparent;
  height: 50px;
  @include border-radius(4px); }
textarea {
  min-height: 150px !important;
  resize: none;
  width: 100%; }
/* form css end*/
