/* category-section css start */
.category-card {
  position: relative;
  &::after {
    position: absolute;
    content: '';
    width: 90%;
    height: 90%;
    top: 5%;
    left: 5%;
    border: 5px solid rgba(#ffffff, 0.15); }
  &__content {
    padding: 30px 30px 30px 40px;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    .title {
      font-size: 28px;
      text-shadow: 0 5px 10px rgba(#000000, 0.2); } } }
/* category-section css end */

/* about section css start */
.about-thumb {
  float: right;
  width: 120%;
  position: relative;
  @media (max-width: 1450px) {
    width: 110%; }
  @media (max-width: 1350px) {
    width: 100%;
    height: 100%;
    padding-bottom: 0; }
  .thumb-one {
    @media (max-width: 1350px) {
      height: 100%; }
    img {
      @media (max-width: 1350px) {
        height: 100%;
        @include object-fit; } } }
  .thumb-two {
    position: absolute;
    top: 150px;
    left: -50px;
    width: 50%;
    @media (max-width: 1550px) {
      display: none; } } }
/* about section css end */

/* feature section css start */
.feature-card {
  padding: 30px 30px;
  background-color: #ffffff;
  @include border-radius(5px);
  box-shadow: $shadow-two;
  @include transition(all 0.3s);
  @include d-flex;
  @media (max-width: 1199px) {
    text-align: center; }
  &:hover {
    @include transform(translateY(-5px));
    background-color: $base-color;
    .feature-card__icon {
      color: #ffffff; }
    .feature-card__content {
      .title {
        color: #ffffff; }
      p {
        color: #ffffff; } } }
  &__icon {
    width: 75px;
    color: $base-color;
    font-size: 72px;
    line-height: 1;
    @include transition(all 0.3s);
    @media (max-width: 1199px) {
      width: 100%; } }
  &__content {
    width: calc(100% - 75px);
    padding-left: 20px;
    @media (max-width: 1199px) {
      width: 100%;
      padding-left: 0;
      margin-top: 20px; }
    .title {
      margin-bottom: 5px;
      @include transition(all 0.3s); }
    p {
      @include transition(all 0.3s); } }
  &.style--two {
    text-align: center;
    &:hover {
      .feature-card__icon {
        background-color: rgba(#ffffff, 0.1); } }
    .feature-card__icon {
      width: 95px;
      height: 95px;
      background-color: rgba($base-color, 0.1);
      font-size: 56px;
      @include d-flex;
      justify-content: center;
      align-items: center;
      @include border-radius(50%);
      margin: 0 auto 30px auto; } } }
/* feature section css end */


/* event section css start */
.event-card {
  @include border-radius(5px);
  overflow: hidden;
  box-shadow: $shadow-two;
  &__content {
    background-color: #ffffff;
    padding: 30px 30px;
    .amount-status {
      margin-top: 15px;
      @include d-flex;
      justify-content: space-between; } }
  &.style--two {
    .event-card__content {
      background-color: $base-color; }
    .title {
      color: #ffffff; }
    .days-left, p {
      color: rgba(#ffffff, 0.9); }
    .progressbar {
      .bar {
        background-color: darken($base-color, 10%); }
      .label {
        background-color: darken($base-color, 10%);
        &::before {
          border-top-color: darken($base-color, 10%); } } }
    .amount-status {
      color: $heading-color; } } }
.event-bar-item {
  margin-top: 50px; }
.progressbar {
  position: relative;
  display: block;
  width: 100%;
  height: 10px;
  background-color: #eeeeee;
  @include border-radius(999px);
  .bar {
    position: absolute;
    width: 0px;
    height: 100%;
    top: 0;
    left: 0;
    background: $base-color;
    overflow: hidden;
    @include border-radius(999px); }
  .label {
    position: absolute;
    top: -40px;
    left: 0;
    padding: 2px 8px;
    height: 30px;
    display: block;
    background-color: $base-color;
    line-height: 28px;
    text-align: center;
    color: #ffffff;
    font-size: 14px;
    @include transform(translateX(-22px));
    @include border-radius(3px);
    &::before {
      position: absolute;
      content: '';
      bottom: -10px;
      left: 50%;
      @include transform(translateX(-50%));
      width: 10px;
      height: 10px;
      border-top: 6px solid $base-color;
      border-bottom: 5px solid transparent;
      border-right: 5px solid transparent;
      border-left: 5px solid transparent; } } }
/* event section css end */

/* event details css start */
.event-details-wrapper {
  padding: 30px;
  background-color: #ffffff;
  border: 5px solid #f1f1f1;
  @media (max-width: 767px) {
    padding: 15px; } }
.vent-details-form {
  label {
    font-size: 14px;
    text-transform: uppercase; } }
.text-small {
  font-size: 12px !important; }
.single-review {
  padding: 15px 0;
  @include d-flex;
  border-bottom: 1px solid #e5e5e5;
  &:first-child {
    padding-top: 0; }
  &:last-child {
    padding-bottom: 0;
    border-bottom: none; }
  .thumb {
    width: 120px; }
  .content {
    width: calc(100% - 120px);
    padding-left: 20px;
    @media (max-width: 575px) {
      width: 100%;
      padding-left: 0;
      margin-top: 20px; } }
  .ratings {
    float: right;
    margin-top: -28px;
    @media (max-width: 575px) {
      float: none;
      margin-top: 10px; }
    i {
      color: #ffb560;
      font-size: 13px; } }
  .date {
    font-size: 14px;
    font-style: italic; } }
/* event details css end */

/* volunteer section css start */
.volunteer-card {
  background-color: #ffffff;
  box-shadow: $shadow-two;
  @include border-radius(5px);
  overflow: hidden;
  &__thumb {
    position: relative;
    overflow: hidden;
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $bg-one;
      opacity: 0;
      @include transition(all 0.3s); } }
  &__content {
    padding: 30px 30px;
    text-align: center;
    @include transition(all 0.3s);
    position: relative;
    @media (max-width: 767px) {
      padding: 30px 20px; }
    img {
      position: absolute;
      left: 0;
      width: 100%;
      top: -18px; } } }
/* volunteer section css end */

/* overview section css start */
.min-height--block {
  min-height: 550px; }
.video-thumb-two {
  position: relative;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-one;
    opacity: 0.65; }
  .video-button {
    top: 50%;
    left: 50%;
    margin-left: -50px;
    margin-top: -50px; } }
.overview-area {
  padding-left: 100px;
  padding-right: 250px;
  @media (max-width: 1750px) {
    padding-right: 120px; }
  @media (max-width: 1450px) {
    padding-left: 50px;
    padding-right: 50px; }
  @media (max-width: 1199px) {
    padding-left: 100px;
    padding-right: 100px; }
  @media (max-width: 767px) {
    padding-left: 50px;
    padding-right: 50px; }
  @media (max-width: 575px) {
    padding-left: 30px;
    padding-right: 30px; }
  .section-title {
    color: #ffffff; }
  p {
    color: #e6e6e6; } }
.counter-card {
  @include d-flex;
  justify-content: center;
  align-items: center;
  padding: 35px 10px;
  text-align: center;
  &__content {
    span {
      font-size: 36px;
      font-family: $heading-font;
      font-weight: 700;
      line-height: 1; }
    p {
      font-size: 16px;
      line-height: 1; } } }
.texture-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  img {
    width: 100%;
    height: 100%; } }
/* overview section css end */

/* faq section css start */
.request-quote {
  padding: 30px;
  background-color: #ffffff;
  @include border-radius(5px);
  border: 1px solid #e5e5e5;
  box-shadow: $shadow-two;
  .title {
    position: relative;
    padding-bottom: 10px;
    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 25px;
      height: 3px;
      background-color: $base-color; } } }
.request-form {
  margin-top: 40px;
  input {
    @include placeholder(#d3d3d3); }
  .form-group + .form-group {
    margin-top: 20px; }
  .nice-select {
    margin-bottom: 30px; } }

/* faq section css end */


/* story section css start */
.story-thumb {
  height: 100%;
  img {
    width: 100%;
    height: 100%;
    @include object-fit; }
  div {
    height: 100%; } }

.story-content {
  position: relative;
  padding: 100px 50px;
  @media (max-width: 575px) {
    padding: 90px 30px; } }
.slick-dots {
  margin-top: 20px;
  @include d-flex;
  justify-content: center;
  li {
    margin: 0 3px;
    &.slick-active {
      button {
        background-color: $base-color; } }
    button {
      font-size: 0;
      width: 12px;
      height: 12px;
      background-color: #ffffff;
      @include border-radius(50%); } } }
/* story section css end */


/* blog section css start */
.blog-post {
  background-color: #ffffff;
  box-shadow: $shadow-two;
  &__content {
    padding: 30px 30px; }
  &__icon {
    font-size: 36px;
    margin-top: 15px; }
  &__title {
    margin-bottom: 25px; }
  &__meta {
    @include d-flex;
    justify-content: space-between;
    li {
      a {
        color: $para-color; } } } }
/* blog section css end */

/* blog-details-section css start */
.blog-details__thumb {
  position: relative;
  img {
    width: 100%; }
  .post__date {
    position: absolute;
    top: 0;
    left: 0;
    width: 75px;
    text-align: center;
    .date {
      font-size: 30px;
      font-weight: 700;
      color: #ffffff;
      background-color: $base-color;
      padding: 10px 5px;
      width: 100%;
      line-height: 1; }
    .month {
      background-color: #ffffff;
      text-transform: uppercase;
      padding: 4px 5px;
      width: 100%;
      line-height: 1;
      font-size: 18px; } } }
.blog-details__content {
  margin-top: 30px;
  p {
    margin-top: 20px; }
  .blog-details__title {
    font-size: 24px; }
  blockquote {
    margin-top: 30px;
    margin-bottom: 0; } }
blockquote {
  font-size: 18px;
  color: $para-color;
  font-style: italic;
  text-align: center;
  padding: 50px 60px;
  background-color: #f6f9fa;
  @media (max-width: 575px) {
    padding: 30px 40px;
    font-size: 16px; } }
.blog-details__footer {
  text-align: center;
  padding: 50px 0;
  border-bottom: 1px solid #e5e5e5;
  .caption {
    font-size: 24px;
    margin-bottom: 20px; }
  .social__links {
    justify-content: center;
    @include d-flex;
    li {
      a {
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        color: $heading-color;
        &:hover {
          background-color: $base-color;
          color: #ffffff; } } } } }
.comments-area {
  margin-top: 100px;
  margin-right: 30px;
  @include border-radius(8px);
  .title {
    text-transform: capitalize;
    margin-bottom: 20px;
    font-size: 24px; } }
.comments-list {
  .single-comment {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; }
    &__thumb {
      width: 100px;
      height: 100px;
      overflow: hidden; }
    &__content {
      width: (calc(100% - 100px));
      padding-left: 20px;
      @media (max-width: 480px) {
        @include flexWidth(100%);
        padding-left: 0;
        margin-top: 15px; }
      .name {
        text-transform: capitalize; }
      .date {
        font-size: 14px;
        font-style: italic; }
      p {
        margin-top: 5px; } } } }
.reply-btn {
  width: 28px;
  height: 28px;
  background-color: #f7f5fb;
  color: $base-color;
  text-align: center;
  line-height: 28px;
  font-size: 13px;
  float: right;
  margin-top: -30px;
  @include border-radius(50%);
  &:hover {
    background-color: $base-color;
    color: #ffffff; }
  i {
    @include transform(rotate(180deg)); } }
.comment-form-area {
  margin-top: 70px;
  margin-right: 30px;
  .title {
    font-size: 24px;
    margin-bottom: 20px; } }
.comment-form {
  input, textarea {
    background-color: #ffffff;
    border: 1px solid #e5e5e5;
    &:focus {
      box-shadow: 0px 3px 18px 2px rgba(46, 25, 204, 0.16);
      border-color: transparent; } } }
.d-pagination {
  .pagination {
    li {
      a {
        padding: 5px;
        @include border-radius(5px!important);
        line-height: 35px; } } } }
/* blog-details-section css end */

/* contact section css start */
.contact-item {
  padding: 0 50px 50px 50px;
  background-color: $base-color;
  text-align: center;
  margin-top: 50px;
  @include border-radius(8px);
  @include transition(all 0.3s);
  &:hover {
    box-shadow: 0 10px 30px 0 rgba($base-color, 0.35);
    @include transform(translateY(-5px)); }
  .icon {
    width: 100px;
    height: 100px;
    margin-left: auto;
    margin-right: auto;
    background-color: #ffffff;
    box-shadow: 0px 3px 18px 2px rgba(46, 25, 204, 0.16);
    @include d-flex;
    align-items: center;
    justify-content: center;
    @include border-radius(50%);
    @include transform(translateY(-50px));
    i {
      font-size: 36px;
      color: $base-color; } }
  .content {
    .title,p,span,a,li {
      color: #ffffff; }
    .title {
      margin-bottom: 20px;
      text-transform: capitalize; }
    a+a {
      margin-top: 5px; } } }
.contact-form-wrapper {
  .title {
    font-size: 40px;
    margin-bottom: 15px; } }
.contact-content {
  @media (max-width: 991px) {
    margin-top: 50px; }
  .caption {
    font-size: 20px;
    text-transform: uppercase;
    @media (max-width: 575px) {
      font-size: 18px; } } }
/* contact section css end */

/* course details css start */
.course-details {
  .course-title {
    font-size: 32px;
    margin-top: 30px; }
  .subtitle {
    margin-top: 30px; }
  p {
    margin-top: 20px; } }
.course-details-list {
  li + li {
    margin-top: 10px; } }
/* course details css end */

/* gallery section css start */
.gallery-card {
  position: relative;
  &:hover {
    .view-btn {
      opacity: 1;
      visibility: visible; } }
  .view-btn {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#000000, 0.65);
    color: #ffffff;
    @include d-flex;
    justify-content: center;
    align-items: center;
    font-size: 42px;
    opacity: 0;
    visibility: none;
    @include transition(all 0.3s); } }
/* gallery section css end */

/* login section css start */
.login-area {
  text-align: center; }
.action-form {
  text-align: left;
  .input-group-text {
    i {
      font-size: 24px; } }
  .form-group {
    margin-bottom: 0; }
  .form-group + .form-group {
    margin-top: 30px; } }
/* login section css end */

/* privacy section css start */
.content-block + .content-block {
  margin-top: 50px; }
.content-block {
  .content__title {
    margin-bottom: 20px; } }
/* privacy section css end */

/* error-section css start */
.error-section {
  height: 100vh;
  @include d-flex;
  justify-content: center;
  align-items: center;
  padding: 100px 0; }
.error-area {
  .title {
    margin-top: 50px;
    @media (max-width: 767px) {
      font-size: 48px; }
    @media (max-width: 575px) {
      font-size: 36px; } }
  .cmn-btn {
    margin-top: 50px; } }
/* error-section css end */
