/* sidebar css start */
.sidebar {
  padding-left: 30px;
  @media (max-width: 991px) {
    padding-left: 0;
    margin-top: 50px; }
  .widget + .widget {
    margin-top: 50px;
    @media (max-width: 767px) {
      margin-top: 30px; } }
  .widget {
    .search-form {
      position: relative;
      input {
        width: 100%;
        border: 1px solid #e5e5e5; }
      .search-btn {
        position: absolute;
        color: $base-color;
        background-color: transparent;
        top: 0;
        right: 0;
        font-size: 18px;
        color: $para-color;
        width: 50px;
        height: 100%; } }
    .widget__title {
      position: relative;
      padding-bottom: 8px;
      margin-bottom: 25px;
      &::after {
        position: absolute;
        content: '';
        left: 0;
        bottom: 0;
        width: 20px;
        height: 2px;
        background-color: $base-color; } } }
  .categories__list {
    .categories__item + .categories__item {
      margin-top: 10px; }
    .categories__item {
      a {
        background-color: #f8f8f8;
        display: block;
        padding: 10px 30px;
        text-transform: capitalize;
        @include transition(all 0.3s);
        color: $para-color;
        &:hover {
          background-color: $base-color;
          color: #ffffff; } } } }
  .archive__list {
    .archive__item + .archive__item {
      margin-top: 8px; }
    .archive__item {
      a {
        color: $para-color; } } }
  .small-post-list {
    .small-post {
      @include d-flex;
      padding: 20px 0;
      border-bottom: 1px solid #e5e5e5;
      &:first-child {
        padding-top: 0; }
      &:last-child {
        padding-bottom: 0;
        border-bottom: none; }
      &__thumb {
        width: 58px; }
      &__content {
        width: calc(100% - 58px);
        padding-left: 20px;
        .post__title {
          a {
            font-size: 16px;
            font-weight: 600; } } } } }
  .tags {
    margin: -5px -2px;
    a {
      font-size: 16px;
      font-weight: 400;
      color: $para-color;
      padding: 10px 20px;
      border: 1px solid #e5e5e5;
      @include transition(all 0.3s);
      margin: 5px 2px;
      text-transform: capitalize;
      &:hover {
        background-color: $base-color;
        color: #ffffff;
        border-color: $base-color; } } } }

.action-sidebar {
  .cmn-accordion2 {
    .card + .card {
      margin-top: 10px; }
    .card-header {
      .text {
        font-size: 16px;
        font-weight: 600;
        color: #ffffff; }
      .acc-btn {
        background-color: $base-color;
        border: none;
        position: relative;
        &.collapsed {
          background-color: #f8f8f8;
          @include transition(all 0.3s);
          .text {
            color: $para-color;
            @include transition(all 0.3s); }
          .plus-icon {
            @include transform(rotate(-90deg));
            color: $para-color; } }
        .plus-icon {
          position: absolute;
          top: 15px;
          right: 22px;
          font-size: 18px;
          color: #ffffff;
          @include transform(rotate(0));
          @include transition(all 0.3s); } } }
    .card-body {
      background-color: #f8f8f8;
      padding: 20px 30px; } }
  .action__list {
    li + li {
      margin-top: 5px; }
    a {
      color: $para-color; } } }
.donation-widget + .donation-widget {
  margin-top: 50px; }
.donation-widget {
  padding: 40px 30px;
  background-color: #ffffff;
  box-shadow: 0 0 10px rgba(#000000, 0.15);
  .donate-item {
    border-right: 1px solid #e5e5e5;
    &:last-child {
      border-right: none; }
    .amount {
      line-height: 1; } }
  &.offline-donate {
    .mail-address {
      font-size: 24px; } } }
.donor-small-list {
  .single {
    @include d-flex;
    align-items: center;
    padding: 15px 0;
    border-bottom: 1px solid #e5e5e5;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0;
      border-bottom: none; }
    .thumb {
      width: 70px;
      img {
        width: 70px;
        height: 70px;
        @include border-radius(50%);
        @include object-fit; } }
    .content {
      width: calc(100% - 70px);
      padding-left: 20px;
      p {
        color: $base-color;
        font-size: 14px;
        text-transform: uppercase; } } } }
/* sidebar css end */
