/* footer section css start */
.footer-section {
  position: relative;
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-one;
    opacity: 0.85;
    z-index: -1; }
  p, li, span, i, a {
    color: #e6e6e6; }
  hr {
    border-color: rgba(#ffffff, 0.15);
    margin-top: 0;
    margin-bottom: 30px; } }
.footer-top {
  padding-top: 200px;
  padding-bottom: 70px;
  @media (max-width: 991px) {
    padding-top: 90px; } }
.footer-action-list {
  li + li {
    margin-top: 10px; }
  li {
    @include d-flex;
    align-items: center;
    i {
      font-size: 24px;
      margin-right: 8px; } } }
.footer-overview-item {
  border-right: 2px solid rgba(#ffffff, 0.15);
  @media (max-width: 575px) {
    border-right: none; }
  &:last-child {
    border-right: none; }
  .amount-number {
    font-size: 28px; }
  p {
    font-size: 14px;
    text-transform: uppercase; } }
.footer-widget {
  &__title {
    color: #ffffff;
    margin-bottom: 20px;
    @media (max-width: 991px) {
      margin-bottom: 15px; } }
  .social-links {
    @include d-flex;
    margin: -3px -5px;
    li {
      margin: 3px 5px;
      a {
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #ffffff;
        border: 1px solid rgba(#ffffff, 0.15);
        @include border-radius(50%);
        @include transition(all 0.3s);
        &:hover {
          background-color: $base-color;
          border-color: $base-color; } } } } }
.short-link-list {
  li + li {
    margin-top: 10px; }
  li {
    a {
      padding-left: 25px;
      position: relative;
      font-size: 14px;
      &::before {
        position: absolute;
        content: "\f101";
        font-family: 'Line Awesome Free';
        top: -2px;
        left: 0;
        color: #e6e6e6;
        font-size: 18px;
        font-weight: 900; } } } }
.subscribe-form {
  @include d-flex;
  background-color: #ffffff;
  input {
    width: calc(100% - 55px);
    background-color: #ffffff;
    border: none;
    height: auto;
    @include border-radius(0); }
  button {
    background-color: $base-color;
    color: #ffffff;
    width: 55px;
    font-size: 32px;
    border: 3px solid #ffffff; } }
.footer-bottom {
  padding-bottom: 30px;
  .link-list {
    @include d-flex;
    margin: -5px -10px;
    li {
      margin: 5px 10px; } } }
/* footer section css end */
